import { BiPlugin } from '../../../types';
import renderNode from '../renderNode';
import toggleQuote from '../commands/toggleQuote';
import * as actions from '../actions';
import onAction from '../handlers/onAction';
import onKeyDown from '../handlers/onKeyDown';
import onCangjieInput from '../handlers/onCangjieInput';
import { queries } from '../queries';
import { moFactory } from '@ali/4ever-plugin-blockquote';

const commands = {
  toggleQuote,
};

function createBlockquotePlugin(config): BiPlugin {
  const blockquoteConfig = config.blockquote || {};
  return {
    ...moFactory({}),
    renderNode,
    commands,
    onAction,
    onKeyDown,
    onCangjieInput,
    queries,
    models: {
      data: {
        shd: [undefined, Object],
      },
    },
  };
}

createBlockquotePlugin.actions = actions;
createBlockquotePlugin.commands = commands;

export default createBlockquotePlugin;
